<template>
  <section class="settings">
    {{ $router.history }}
    <div class="settings__close" @click="close" v-if="$isMobile">
      <img src="@/assets/icons/clear.svg" alt="Close" />
    </div>
    <h1 class="settings__title page__title">Мои данные</h1>
    <div class="settings-fields fields page-wrapper">
      <div class="settings-content-row fields-row">
        <base-input
          :label="'Имя и фамилия'"
          :id="'settingsName'"
          v-model="data.fio"
        />
      </div>
      <div class="settings-content-row fields-row">
        <base-input
          :label="'Телефон'"
          :id="'settingsPhone'"
          v-model="data.phone_number"
        />
      </div>
      <div class="settings-content-row">
        <base-input
          :label="'Электронная почта'"
          :id="'settingsEmail'"
          v-model="data.email"
        />
      </div>
    </div>
    <div class="settings-buttons page-buttons _save">
      <div class="settings-buttons-save _save__button">
        <base-button :type="'filled'" @clicked="save"> Сохранить </base-button>
      </div>
    </div>
  </section>
</template>

<script>
import { useMeta } from "vue-meta";

export default {
  name: "Settings",
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.prevRoute = from;
    });
  },
  data() {
    return {
      data: {
        fio: "",
        phone_number: "",
        email: "",
      },
      prevRoute: null,
    };
  },
  setup() {
    useMeta({ title: "Мои Данные" });
  },
  mounted() {
    this.getSettings();
  },
  methods: {
    save() {
      this.$api
        .patch(`${this.$api.defaults.API}/user`, this.data)
        .then((res) => {
          console.log("save");
          console.log(res);
        });
    },
    close() {
      this.$router.push({ name: this.prevRoute.name || "index" });
    },
    getSettings() {
      this.$api.get(`${this.$api.defaults.API}/user/me`).then((res) => {
        for (const [key] of Object.entries(this.data)) {
          this.data[key] = res.data[key];
        }
      });
    },
  },
};
</script>

