  &-content {
    margin-bottom: 50px;
    &__title {
      font-family: $regular;
      font-size: 16px;
      font-weight: 400;
    }
  }
}
@media (max-width: 992px) {
  .settings {
    width: 100%;
    min-height: 100vh;
